@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components
{   
    ::-webkit-scrollbar
    {
      width: 4px;
      height: 5px;
    }
    ::-webkit-scrollbar-track
    {
      @apply bg-black;
    }
    ::-webkit-scrollbar-thumb
    {
      transition: 1s;
      background: #6B21A8;
      border-radius: 15px;
    }

  .App
  {
    @apply pt-10 pb-10 md:p-20 justify-center items-center text-center min-h-screen bg-[url(./img/texture.svg)] ;
  }

  .navbar
  {
    @apply flex items-center justify-between w-[92%] mx-auto ;
  }

  .bottone
  {
    @apply text-purple-800 font-bold text-xl hover:text-white duration-[0.8s];
  }

  .bottone:hover
  {
    transform: scale(1.1);
    transition: 0.8s;
  }

  .bottonec
  {
    @apply text-black font-bold text-xl bg-purple-800 rounded-2xl pt-2 pb-2 pl-4 pr-4 duration-[0.8s];
  }

  .bottonec:hover
  {
    transform: scale(1.1);
    transition: 0.8s;
  }

  .logo
  {
    transition: 0.5s;
  }

  .logo:hover
  {
    transform: scale(1.1);
    transition: 0.5s;
  }

  .home
  {
    @apply text-center mt-[120px];
  }

  .container-grid
  {
     @apply flex flex-wrap;
  }

  .item
  {
     @apply grow basis-20 pr-5 pl-5 mt-5;
  }

  .card
  {
    transition: 0.5s;
  }

  .card:hover
  {
    transform: scale(0.9);
    transition: 0.5;
  }

  .footer
  {
    @apply flex items-center justify-center text-center min-w-full sticky bottom-0;
  }
}